.NavbarItems {
    background: linear-gradient(90deg, rgb(10, 4, 65) 0%, rgb(38, 28, 230) 100%);
    height: 80px;
    display:flex;
    justify-content: center;
    align-items: center;
    font-size: 1.2rem;
}


.navbar-logo {
    color: darkblue;
    justify-self: start;
    margin-left: 20px;
    cursor: pointer;
}

.fa-react {
    margin-left: 0.5rem;
    font-size: 1.6rem;
}

.nav-menu {
    display: grid;
    grid-template-columns:repeat(7, auto);
    grid-gap: 10px;
    list-style: none;
    text-align: center;
    width: 70vw;
    justify-content: end;
    margin-right: 2rem;
}

.nav-links {
    color: white;
    text-decoration:  none;
    padding: 0,25rem 0,5rem;
    text-shadow: 2px 2px black ;
    text-align: center;
    font-size: 15px;
    border-style: solid;
    border-color: black;
    border-radius: 50px;
    border-width: 3px;
}
.nav-links:hover {
    background-color: darkblue;
    border-radius: 2px;
    transition: all 0.2s ease-out;
}
